*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.main{
  display: flex;
  
}
.sidebar{
  background-color: red;
  width: 15%;
  height: 100vh;
}
.header{
  background-color: green;
  height: 10vh;
  width: 84%;
  margin-left: 2px;
}

.spinner{
  height: 100vh;
  display: flex;
  justify-content: start;
  align-items: center;
}
@media screen and (max-width:568px) {
  #available{
    width: 100vw;
    background-color: red;
  }
}